import React, { FC, memo, Suspense } from 'react';
import { RouteInfo, renderRoutes } from 'helpers/routes';

export const RoutesComponent: FC<RoutesProps> = ({ basePath, routes }) => (
  <Suspense fallback="Loading...">{renderRoutes(basePath, routes)}</Suspense>
);

const Routes = memo(RoutesComponent);
export default Routes;

export interface RoutesProps {
  basePath: string;
  routes: RouteInfo[];
}
