import React, { ComponentType } from 'react';
import {
  RouteComponentProps,
  NavLink,
  Route,
  RouteProps,
} from 'react-router-dom';

const createPath = (basePath: string, path: string) =>
  `${basePath.replace(/\/$/, '')}/${path}`;

export const renderLinks = (basePath: string, routes: RouteInfo[]) =>
  routes.map(({ name, path }) => (
    <NavLink key={name} to={createPath(basePath, path)}>
      {name}
    </NavLink>
  ));

export const renderRoutes = (basePath: string, routes: RouteInfo[]) =>
  routes.map(({ name, path, component, exact }) => (
    <Route<RouteProps>
      key={name}
      path={createPath(basePath, path)}
      component={component}
      exact={exact}
    />
  ));

export interface RouteInfo {
  name: string;
  path: string;
  component: ComponentType<RouteComponentProps>;
  exact?: boolean;
}
