import React, { FC, memo } from 'react';
import classnames from 'classnames';
import { RouteInfo, renderLinks } from 'helpers/routes';
import classes from './styles.module.scss';

export const NavImpl: FC<NavProps> = ({ className, basePath, routes }) => (
  <nav className={classnames(classes.Nav, className)}>
    {renderLinks(basePath, routes)}
  </nav>
);

const Nav = memo(NavImpl);
Nav.displayName = 'Nav';
export default Nav;

export interface NavProps {
  className?: string;
  basePath: string;
  routes: RouteInfo[];
}
